import Header from './components/Header';
import Main from './components/Main';
import Basket from './components/Basket';
import data from './data';
import { useState } from 'react';
function App() {
  let today = new Date();
  let datetodayinddmmyyyy=today.getDate() + "-"+ parseInt(today.getMonth()+1) +"-"+today.getFullYear();
  const { products } = data;
  const [cartItems, setCartItems] = useState([]);
  const onAdd = (product) => {
    const exist = cartItems.find((x) => x.num === product.num);
    if (exist) {
      setCartItems(
        cartItems.map((x) =>
          x.num === product.num ? { ...exist, qty: exist.qty + 1,date:datetodayinddmmyyyy} : x
        )
      );
    } else {
      setCartItems([...cartItems, { ...product, qty: 1 ,date:datetodayinddmmyyyy}]);
    }
  };
  const onRemove = (product) => {
    const exist = cartItems.find((x) => x.num === product.num);
    if (exist.qty === 1) {
      setCartItems(cartItems.filter((x) => x.num !== product.num));
    } else {
      setCartItems(
        cartItems.map((x) =>
          x.num === product.num ? { ...exist, qty: exist.qty - 1,date:datetodayinddmmyyyy } : x
        )
      );
    }
  };
  return (
    
    <div className="App">
      <Header countCartItems={cartItems.length}></Header>
      <div className="row">
        <Main products={products} onAdd={onAdd}></Main>
        <Basket
          cartItems={cartItems}
          onAdd={onAdd}
          onRemove={onRemove}
        ></Basket>
      </div>
    </div>
    
  );
}

export default App;
